    margin-right: 10px
  }
  .recipe-data {
    height: 50px;
    flex-grow: 1;
    color: var(--default-text-color);
  }
  .recipe-name {
    font-size: 18px;
  }
  .recipe-ingridients {
      font-size: 13px;
      margin-top: 10px
  }
  i {
    display: block;
    height: 14px;
    float: left;
    margin-right: 8px;
